@import './menu.scss';
@import './footer.scss';

// @mixin set-margin($direction, $value) {
//     margin-#{$direction}: $value;
// }
html, body, div, p, img, h1, h2, h3, h4, h5 {
    box-sizing: border-box;
}

body{
    margin: 0;
    padding: 0;
    font-size:16px;
    font-family: sans-serif;
    color: lightblue;
}

h1 {
    font-size: 2em;
}

.container {
    width: 980px;
    max-width: 100%;
    margin: 0 auto;
    padding: 0.5em;
}

.main-image {
    background-image: url('../images/thumbnail.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
    width: 100%;
    height: 600px;
    margin: 1em 0;
}

$icon-size: 64px;
.icon{
    width: $icon-size;
    height: $icon-size;
    padding: 4px;
    position: absolute;
    top: 1em;
    left: 1em;
}

